@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");

* {
  scroll-behavior: smooth;
  font-family: Inter, "Inter", Menlo, Monaco, Consolas, "Courier New", monospace;
  transition: all 0.09s;
}

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  .x_container {
    @apply w-[95%] lg:w-[90%] 2xl:w-[1500px] mx-auto;
  }
}

.x_container {
  margin: 0 auto;
  width: 95%;
}

@media (min-width: 1023) {
  .x_container {
    width: 93%;
  }
}

@media (min-width: 1240px) {
  .x_container {
    width: 90%;
  }
}

@media (min-width: 2000px) {
  .x_container {
    width: 1800px;
  }
}

@media (min-width: 1024px) {
  .adjust_bg::after {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    content: "";
    width: 50%;
    height: 100%;
    background: #ffffff;
    z-index: 1;
  }
  .adjust_bg::before {
    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    content: "";
    width: 50%;
    height: 100%;
    background: #f2f2f7;
    z-index: 1;
  }
}
